@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600&family=Lexend+Deca&display=swap");
*,
*::after,
*::before {
  margin: 0;
}

body {
  font-family: "Lexend Deca", sans-serif;
  background: #090b1a;
  line-height: 1.6;
}

img {
  max-width: 100%;
}

.card {
  position: relative;
  width: 90%;
  max-width: 1200px;
  margin: 4em auto;
  background: #1b1938;
  border-radius: 15px;
}
.card .mobile-image-container {
  position: relative;
  display: inline-block;
}
.card .mobile-image-container .image {
  border-radius: 15px 15px 0 0;
  filter: sepia(100%) hue-rotate(160deg);
}
.card .stats {
  color: white;
  text-align: center;
  padding: 1em 1em;
}
.card .stats h2 {
  font-family: "Inter", sans-serif;
  font-size: 2rem;
}
.card .stats h2 .purple {
  color: #aa5cdb;
}
.card .stats .description {
  margin: 1em 0;
  color: rgba(255, 255, 255, 0.75);
}
.card .stats .numbers {
  margin-bottom: 1em;
}
.card .stats .numbers .number {
  font-weight: 700;
  font-size: 1.5rem;
}
.card .stats .numbers .number-category {
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
}
.card .desktop-image-container {
  display: none;
}

@media (min-width: 600px) {
  body {
    height: 100vh;
  }

  .card {
    position: relative;
    top: 10%;
    padding: 0;
    display: grid;
    grid-template-columns: 55% 45%;
  }
  .card .mobile-image-container {
    display: none;
  }
  .card .stats {
    text-align: start;
    padding: 4em;
  }
  .card .stats .number-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .card .desktop-image-container {
    display: block;
  }
  .card .desktop-image-container .image {
    width: 100%;
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 0 15px 15px 0;
  }
}